<template>
  <div class="text-center" id="app">
    <h1>Word Translator</h1>
    
    <br>
    <TranslateForm v-on:formSubmit="translateText"></TranslateForm>
    <br>
    <TranslateOutput v-text="translatedText"></TranslateOutput>
    <h5 class="box">&copy;Shivam Goraksha</h5>
  </div>
  
</template>

<script>

import TranslateForm from './components/TranslateForm'
import TranslateOutput from './components/TranslateOutput'

export default {
  name: 'app',
  components:{
    TranslateForm,
    TranslateOutput

  },
  data:function(){
    return {
      translatedText:''
    }
  },
  methods:{
    translateText:function(text,language){
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20170821T205521Z.f9b752747d56a7f4.fcaa12b3b6636a1ba7f169b301621e0b26fe7d2f&lang='+language+'&text='+text).then((response)=>{
        this.translatedText=response.body.text[0];
      });

    }
  }
}
</script>

<style>
#app {


}
.box {
    height: 50px;
    width: 187px;
    margin-top: 300px;
    margin-left: 5.5px;
    margin-bottom: 1px;
    
    color: grey;

}
</style>



